import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/currency/currency/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";

// const Panel = Collapse.Panel;
// const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class Currency extends Component {
  state = {
    filter: {
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getCurrencies } = this.props;
    getCurrencies();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.currency.error) {
      notification("error", this.props.currency.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.currency.success) {
      notification("success", this.props.currency.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    //console.log(value);
    const { getCurrencies } = this.props;
    this.setState({
      filter: {
        pagination: null
      }
    });
    getCurrencies(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getCurrencies } = this.props;
    this.setState({
      filter: {
        pagination: null
      }
    });
    this.setState({ search: null });
    getCurrencies();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getCurrencies } = this.props;
    //const { current_page } = this.props.currency;
    const { filter } = this.state;
    let string = "";

    // if (filter.id_account_sale) {
    //   string += "id_account_sale=" + filter.id_account_sale;
    // }
    // if (filter.id_account_purchase) {
    //   string
    //     ? (string += "&id_account_purchase=" + filter.id_account_purchase)
    //     : (string += "id_account_purchase=" + filter.id_account_purchase);
    // }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getCurrencies(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getCurrencies } = this.props;
    this.setState({
      filter: {
        pagination: null
      }
    });
    this.setState({ search: null });
    getCurrencies();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateCurrency(id);
    } else {
      this.props.deactivateCurrency(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getCurrencies } = this.props;
    const { search } = this.props.currency;
    const { filter } = this.props.currency;
    getCurrencies(page, search, filter);
  };

  // Definisi kolom2 serta field2 API yang dipakai tabel
  columns = [
    {
      title: "Code",
      dataIndex: "currency_code",
      rowKey: "currency_code",
      width: "5%",
      render: text => <span>{text}</span>
    },
    {
      title: "Name",
      dataIndex: "currency_name",
      rowKey: "currency_name",
      width: "10%",
      render: text => <span>{text}</span>
    },
    {
      title: "Base",
      dataIndex: "is_base",
      rowKey: "is_base",
      width: "5%",
      align: "center",
      render: text => {
        let className;
        let display;
        if (text === 1) {
          className = "primary";
          display = "Yes";
        } else {
          className = "nonactive";
          display = "No";
        }
        return <StatusTag className={className}>{display}</StatusTag>;
      }
    },
    {
      title: "Status",
      dataIndex: "is_active",
      rowKey: "is_active",
      width: "10%",
      align: "center",
      render: text => {
        let className;
        let display;
        if (text === 1) {
          className = "active";
          display = "Active";
        } else {
          className = "nonactive";
          display = "Inactive";
        }
        return <StatusTag className={className}>{display}</StatusTag>;
      }
    }
  ];

  listAction = {
    title: "",
    dataIndex: "id_currency",
    rowKey: "id_currency",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="currencies.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText="Yes"
            cancelText="No"
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="currencies.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText="Yes"
            cancelText="No"
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // console.log(this.state.filter);

    // Data setup
    const { currencies } = this.props.currency;
    //console.log(this.props.currency); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Currency") && this.columns.length === 4) {
      this.columns.push(this.listAction);
    }

    Object.keys(currencies).map((currency, index) => {
      return dataSource.push({
        ...currencies[currency],
        key: currency
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.currency;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="currencies.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {/* {permissions.includes("Create Currency") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="currencies.button.add" />
                  </Button>
                </Link>
              ) : null} */}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          <div className="isoInvoiceTableBtn">
            <Search
              placeholder="Name"
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                // width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {currencies.length === 0 && !this.props.currency.loading ? (
              <Empty
                description={<IntlMessages id="currencies.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={this.columns}
                    loading={this.props.currency.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    currency: state.currency,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(Currency);
